@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
:root {
  --orange: #ffa500;
}
*::selection {
  background: var(--orange);
  color: #fefefe;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 6rem;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  width: 0.5rem;
}
html::-webkit-scrollbar-track {
  background: #333;
}
html::-webkit-scrollbar-thumb {
  background: var(--orange);
  border-radius: 5rem;
}
.dark .Pages {
  background: #111;
  overflow: hidden;
}
.dark .Pages .heading h1 {
  color: #fff;
  font-size: 4rem;
}
.dark .Pages .btn {
  color: #111;
  display: inline-block;
  margin-top: 1rem;
  background: var(--orange);
  padding: 0.8rem 2rem;
  border: 0.2rem solid var(--orange);
  border-radius: 5rem;
  cursor: pointer;
  font-size: 2rem;
  transition: 0.3s;
}
.dark .Pages .btn:hover {
  color: var(--orange);
  background: rgba(114, 74, 0, 0.2);
}
.dark .Pages .btn-toggle {
  position: absolute;
  right: 20%;
  display: flex;
  color: var(--orange);
  padding: 0.5rem 0.5rem;
  border: 0.1em solid var(--orange);
  border-radius: 50%;
  cursor: pointer;
  font-size: 2em;
  transition: 0.3s;
  transition-delay: 1s;
  transition-duration: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  bottom: 1.7rem;
}
.dark .Pages .banner .content p {
  color: #aaa;
  max-width: 60rem;
  margin: 1rem auto;
  line-height: 2;
  font-size: 1.4em;
}
.dark .Pages .footer .box-container .box h3 {
  color: #fff;
  font-size: 2.2rem;
  padding: 1rem 0;
}
.dark .Pages .footer .box-container .box p {
  color: #aaa;
  font-size: 1.4em;
  padding: 1rem 0;
  line-height: 2;
}
.dark .Pages .footer .box-container .box .links {
  color: #aaa;
  font-size: 1.4em;
  padding: 1rem 0;
  display: block;
}
.dark .Pages .footer .box-container .box form .email {
  color: #aaa;
  width: 100%;
  border-radius: 5rem;
  border: 0.2rem solid var(--orange);
  background: none;
  font-size: 1.5rem;
  text-transform: none;
  margin-bottom: 1rem;
  padding: 1.2rem 1.4rem;
}
.dark .Pages .credit {
  color: #aaa;
  background: #222;
  text-align: center;
  font-size: 2rem;
  padding: 2rem 1rem;
}

.dark .Pages .heading {
  text-align: center;
  margin-bottom: 2rem;
}
.dark .Pages .heading span {
  color: var(--orange);
  font-weight: 600;
  font-size: 2em;
}
.dark .Pages .header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #222;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 1.5rem 9%;
}
.dark .Pages .header .btn {
  margin-top: 0;
  padding: 0.5rem 1rem;
  color: #333;
}
.dark .Pages .header .btn:hover {
  color: #efefef;
}
.dark .Pages .logo {
  font-size: 2.5rem;
  color: #fff;
  font-weight: bolder;
}
.dark .Pages .logo span {
  color: var(--orange);
}
.dark .Pages #menu-btn {
  font-size: 2.5rem;
  color: #fff;
  cursor: pointer;
  display: none;
}
.dark .Pages .banner .content span {
  font-size: 2rem;
  color: var(--orange);
}
.dark .Pages .banner .content h3 {
  font-size: 4rem;
  color: #fff;
  margin-top: 1rem;
}
.dark .Pages .footer {
  background: #222;
}
.dark .Pages .lower{
  text-transform: none;
}
.dark .Pages .footer .box-container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(25rem, 1fr)) [auto-fit];
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 1.5rem;
}
.dark .Pages .footer .box-container .box {
  padding: 1rem 0;
}
.dark .Pages .footer .box-container .box .logo {
  padding-bottom: 1rem;
}
.dark .Pages .footer .box-container .box p i {
  padding-right: 0.5rem;
  color: var(--orange);
}
.dark .Pages .footer .box-container .box .share {
  padding-top: 1rem;
  display: flex;
  flex-direction: row;
}
.dark .Pages .footer .box-container .box .share a {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4rem;
  font-size: 2rem;
  border-radius: 50%;
  font-size: 1.7rem;
  border: 0.2rem solid var(--orange);
  color: var(--orange);
  margin-right: 0.5rem;
  text-align: center;
    display: flex;
  align-items: center;
  align-items: center;
  justify-content: center;
}
.dark .Pages .footer .box-container .box .share a:hover {
  background: var(--orange);
  color: #111;
}
.dark .Pages .footer .box-container .box .links:hover {
  color: var(--orange);
}
.dark .Pages .footer .box-container .box .links:hover i {
  padding-right: 2rem;
}
.dark .Pages .footer .box-container .box .links i {
  padding-right: 0.5rem;
  color: var(--orange);
}
.dark .Pages .footer .btn {
  color: #333;
}
.dark .Pages .footer .btn:hover {
  color: var(--orange);
}
.dark .Pages .credit span {
  color: var(--orange);
}
@media (max-width: 1030px) {
  .dark .Pages .btn-toggle {
    right: 22%;
  }
}
@media (max-width: 991px) {
  .dark .Pages .btn-toggle {
    right: 18%;
  }
  html {
    font-size: 55%;
  }
  .dark .Pages .header {
    padding: 1.5rem 2rem;
  }
  .dark .Pages section {
    padding: 3rem 2rem;
  }
}
@media (max-width: 768px) {
  .dark .Pages .book-form form span h1 {
    font-size: 1.2em;
  }
  .dark .Pages .btn-toggle {
    right: 30%;
  }
  .dark .Pages #menu-btn {
    display: inline-block;
  }
  .dark .Pages .header .navbar {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 99%;
    left: 0;
    right: 0;
    background: #222;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  .dark .Pages .header .navbar.active {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .dark .Pages .home {
    margin-top: 10rem;
  }
 .dark .Pages .home .content span {
    font-size: 8vw;
  }
  .dark .Pages .home .content h3 {
    font-size: 9vw;
  }
}
@media (max-width: 450px) {
  .dark .Pages .contact .row form .inputBox input {
    width: 100%;
  }
  .dark .Pages .btn-toggle {
    right: 30%;
  }
  html {
    font-size: 50%;
  }
  .dark .Pages .about .content h3 {
    font-size: 3.5rem;
  }
  .dark .Pages .banner .content h3 {
    font-size: 2.7rem;
  }
}
@media (max-width: 350px) {
  .dark .Pages .btn-toggle {
    right: 35%;
  }
}
@media (max-width: 300px) {
  .dark .Pages .btn-toggle {
    right: 38%;
    font-size: 1.5em;
  }
}
