@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
:root {
  --orange: #ffa500;
}
*::selection {
  background: var(--orange);
  color: #fefefe;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 6rem;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  width: 0.5rem;
}
html::-webkit-scrollbar-track {
  background: #333;
}
html::-webkit-scrollbar-thumb {
  background: var(--orange);
  border-radius: 5rem;
}
.dark .HomePages{
  background-color: #333;
  overflow: hidden;
}
.dark .HomePages .btn {
  color: #111;
  display: inline-block;
  margin-top: 1rem;
  background: var(--orange);
  padding: 0.8rem 3rem;
  border: 0.2rem solid var(--orange);
  cursor: pointer;
  font-size: 1.7rem;
}
.dark .HomePages .btn:hover {
  background: rgba(114, 74, 0, 0.2);
  color: var(--orange);
  font-weight: 600;
}
.dark .HomePages header .search-bar-container #search-bar {
  background: #efefef;
  width: 100%;
  padding: 1rem;
  text-transform: none;
  color: #333;
  font-size: 1.7rem;
  border-radius: 0.5rem;
}
.dark .HomePages #menu-bar {
  border: 0.1em solid var(--orange);
  color: #fefefe;
  border-radius: 0.5rem;
  font-size: 3rem;
  padding: 0.5rem 1.2rem;
  cursor: pointer;
  display: none;
  width: 40px;
  height: 40px;
}
.dark .HomePages .home .content h3 {
  color: var(--orange);
  font-size: 4em;
  text-transform: uppercase;
  text-shadow: 0 0.3em 0.5em rgba(0, 0, 0, 0.3);
}
.dark .HomePages .home .controls .vid-btn {
  background: #888;
  height: 2rem;
  width: 2rem;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 0.5rem;
}
.dark .HomePages .book .row form {
  flex: 1 1 40rem;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 1em 2em rgba(0, 0, 0, 0.15);
}
.dark .HomePages select {
  background: #333;
  color: var(--orange);
  max-width: 100%;
  width: 100%;
  padding-bottom: 0.25em;
  padding-top: 0.25em;
  font-size: 2em;
  font-weight: 500;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}
.dark .HomePages .book .row form .inputBox input {
  border: 0.1rem solid var(--orange);
  background: #333;
  color: #efefef;
  width: 100%;
  padding: 1rem;
  font-size: 1.7rem;
  text-transform: none;
  border-radius: 10px;
}
.dark .HomePages .book .row form .inputBox h3 {
  color: var(--orange);
  font-size: 2rem;
  padding: 1rem 0;
}
.dark .HomePages .packages .box-container .box .content h3 {
  color: var(--orange);
  font-size: 2rem;
}
.dark .HomePages .packages .box-container .box .content h3 i {
  color: var(--orange);
}
.dark .HomePages .packages .box-container .box .content p {
  color: #999;
  font-size: 1.7rem;
  padding: 1rem 0;
}
.dark .HomePages .packages .box-container .box .content .price {
  color: var(--orange);
  font-size: 2rem;
  padding-top: 1rem;
}
.dark .HomePages .packages .box-container .box .content .price span {
  color: #888;
  font-size: 1.5rem;
  text-decoration: line-through;
}
.dark .HomePages .services .box-container .box h3 {
  font-size: 2.5rem;
  color: var(--orange);
}
.dark .HomePages .services .box-container .box p {
  color: #888;
  font-size: 1.5rem;
  padding: 1rem 0;
}
.dark .HomePages .review .box h3 {
  font-size: 2.5rem;
  color: var(--orange);
}
.dark .HomePages .review .box p {
  color: #999;
  font-size: 1.5rem;
  padding: 1rem 0;
}
.dark .HomePages .contact .row form .inputBox input,
.dark .HomePages .contact .row form textarea {
  color: #efefef;
  background: #333;
  width: 49%;
  margin: 1rem 0;
  padding: 1rem;
  font-size: 1.7rem;
  text-transform: none;
  border: 0.1rem solid var(--orange);
}
.dark .HomePages .footer .box-container .box h3 {
  color: var(--orange);
  font-size: 2.5rem;
  padding: 0.7rem 0;
}
.dark .HomePages .footer .credit span {
  color: var(--orange);
}
.dark .HomePages .rate .row form .inputBox textarea {
  color: #efefef;
  background: #333;
  border: 0.1rem solid var(--orange);
  text-transform: none;
}
.dark .HomePages section {
  padding: 2rem 9%;
}
.dark .HomePages .heading {
  text-align: center;
  padding: 2.5rem 0;
}
.dark .HomePages .heading span {
  font-size: 3.5rem;
  background: rgba(255, 165, 0, 0.2);
  color: var(--orange);
  border-radius: 0.5rem;
  padding: 0.2rem 1rem;
}
.dark .HomePages .heading span.space {
  background: 0 0;
}
.dark .HomePages header {
  position: fixed;
  max-width: 100%;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: #333;
  filter: drop-shadow(0px 2px 5px #000);
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  align-content: center;
}
.dark .HomePages header .logo {
  font-size: 2.5rem;
  font-weight: bolder;
  color: #fefefe;
  text-transform: uppercase;
}
.dark .HomePages header .logo span {
  color: var(--orange);
}
.dark .HomePages header .navbar a {
  color: #fefefe;
  font-size: 2rem;
  margin: 0 0.8rem;
}
.dark .HomePages header .navbar a:hover {
  color: var(--orange);
}
.dark .HomePages header .icons i {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  color: #fefefe;
  cursor: pointer;
  width: 25px;
  height: 25px;
}
.dark .HomePages #search-btn.fa-times {
  padding: 12px;
  border-radius: 50%;
  border: 2px solid var(--orange);
}
.dark .HomePages header .icons i:hover {
  color: var(--orange);
}
.dark .HomePages .icons {
  display: flex;
  width: 115px;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
.dark .HomePages header .search-bar-container {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding: 1.5rem 2rem;
  background: #333;
  border-top: 0.1rem solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  z-index: 1001;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}
.dark .HomePages header .search-bar-container.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.dark .HomePages header .search-bar-container label {
  color: var(--orange);
  cursor: pointer;
  font-size: 3rem;
  margin-left: 1.5rem;
}
.dark .HomePages header .search-bar-container label:hover {
  color: var(--orange);
}

.dark .HomePages .login-form-container {
  position: fixed;
right: -120%;
top: 0;
  z-index: 10000;
  min-height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
}
.dark .HomePages .login-form-container.active {
  right: 0;
  top: 0;
}
.dark .HomePages .login-form-container form {
  margin: 2rem;
  padding: 1.5rem 2rem;
  border-radius: 0.5rem;
  background: #333;
 max-width: 400px;

}
.dark .HomePages .login-form-container form .profile{
  display: flex;
  justify-content: space-between;
}
.dark .HomePages .login-form-container #form-close {
  position: absolute;
  top: 2rem;
  right: 3rem;
  font-size: 4rem;
  color: #fefefe;
  cursor: pointer;
}
.dark .HomePages .login-form-container form .profile img{
  width: 100px;
  margin-right:20px ;
}
.dark .HomePages .login-form-container form .profile div{
display: flex;
flex-direction: column;
}
.dark .HomePages .login-form-container form .profile div h2{
font-size: 2em;
margin-bottom: 5px;
color: var(--orange);
}
.dark .HomePages .login-form-container form .profile div p{
font-size: 1.5em;
margin-left: 5px;
color: #efefef;
}
.dark .HomePages .login-form-container form .btn{
width: 100px;
height: auto;
font-size: 1.5em;
padding: 3px;
}
.dark .HomePages .home {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  position: relative;
  z-index: 0;
}
.dark .HomePages .home .content {
  text-align: center;
}
.dark .HomePages .home .content p {
  font-size: 2.5rem;
  color: #fefefe;
  padding: 0.5rem 0;
}
.dark .HomePages .home .video-container video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.dark .HomePages .home .controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-radius: 5rem;
  background: rgba(0, 0, 0, 0.5);
  position: relative;
  top: 5rem;
}
.dark .HomePages .home .controls .vid-btn.active {
  background: var(--orange);
}
.dark .HomePages .book .row {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
}
.dark .HomePages .book .row .image {
  flex: 1 1 40rem;
}
.dark .HomePages .book .row .image img {
  width: 100%;
}
.dark .HomePages .book .row form .inputBox {
  padding: 0.5rem 0;
}
.dark .HomePages .packages .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
.dark .HomePages .packages .box-container .box {
  flex: 1 1 30rem;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
}
.dark .HomePages .packages .box-container .box img {
  height: 25rem;
  width: 100%;
  object-fit: cover;
}
.dark .HomePages .packages .box-container .box .content {
  padding: 2rem;
}
.dark .HomePages .packages .box-container .box .content .stars i {
  font-size: 1.7rem;
  color: var(--orange);
}
.dark .HomePages .services .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.dark .HomePages .services .box-container .box {
  flex: 1 1 30rem;
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center;
}
.dark .HomePages .services .box-container .box i {
  padding: 1rem;
  font-size: 5rem;
  color: var(--orange);
}
.dark .HomePages .services .box-container .box:hover {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
}
.dark .HomePages .gallery .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.dark .HomePages .gallery .box-container .box {
  overflow: hidden;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border: 1rem solid #fefefe;
  border-radius: 0.5rem;
  flex: 1 1 30rem;
  height: 25rem;
  position: relative;
}
.dark .HomePages .gallery .box-container .box img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.dark .HomePages .gallery .box-container .box .content {
  position: absolute;
  top: -100%;
  left: 0;
  height: 100%;
  width: 100%;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  padding: 2rem;
  padding-top: 5rem;
}
.dark .HomePages .gallery .box-container .box:hover .content {
  top: 0;
}
.dark .HomePages .gallery .box-container .box .content h3 {
  font-size: 2.5rem;
  color: var(--orange);
}
.dark .HomePages .gallery .box-container .box .content p {
  font-size: 1.5rem;
  color: #eee;
  padding: 0.5rem 0;
}
.dark .HomePages .review .review-slider {
  max-width: 100%;
  overflow: hidden;
  padding-bottom: 2rem;
}
.dark .HomePages .review .box {
  padding: 2rem;
  text-align: center;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
}
.dark .HomePages .review .box img {
  height: 13rem;
  width: 13rem;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1rem;
}
.dark .HomePages .review .box .stars i {
  color: var(--orange);
  font-size: 1.7rem;
}
.dark .HomePages .contact .row {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
}
.dark .HomePages .contact .row .image {
  flex: 1 1 35rem;
}
.dark .HomePages .contact .row .image img {
  width: 100%;
}
.dark .HomePages .contact .row form {
  flex: 1 1 50rem;
  padding: 2rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
}
.dark .HomePages .contact .row form .inputBox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.dark .HomePages .contact .row form textarea {
  height: 15rem;
  resize: none;
  width: 100%;
}
.dark .HomePages .brand-container {
  overflow: hidden;
  max-width: 100%;
  text-align: center;
}
.dark .HomePages .footer {
  background: #333;
}
.dark .HomePages .footer .box-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.dark .HomePages .footer .box-container .box {
  padding: 1rem 0;
  flex: 1 1 25rem;
}
.dark .HomePages .footer .box-container .box p {
  font-size: 1.5rem;
  padding: 0.7rem 0;
  color: #eee;
}
.dark .HomePages .footer .box-container .box a {
  display: block;
  text-decoration: none;
  font-size: 1.5rem;
  padding: 0.7rem 0;
  color: #eee;
}
.dark .HomePages .footer .box-container .box a:hover {
  color: var(--orange);
  text-decoration: underline;
}
.dark .HomePages .footer .credit {
  text-align: center;
  padding: 2rem 1rem;
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
  color: #fefefe;
  border-top: 0.1rem solid rgba(255, 255, 255, 0.2);
}
.dark .HomePages .brand-slider {
  width: 100%;
}
.dark .HomePages .review-slider {
  width: 100%;
}
.dark .HomePages .swiper-slide img{
  border-radius: 5px;
  overflow: hidden;
  width: 30%;
  height: 30%;
}
@media (max-width: 1450px) {
  html {
    font-size: 60%;
  }
}
@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
  .dark .HomePages .brand-slider {
    width: 100%;
  }
}
@media (max-width: 991px) {
  .dark .HomePages header {
    padding: 2rem;
  }
  .dark .HomePages section {
    padding: 2rem;
  }
  .dark .HomePages .brand-slider {
    width: 100%;
  }
}
@media (max-width: 768px) {
  .dark .HomePages .swiper-slide img{
  border-radius: 5px;
  overflow: hidden;
  width: 100%;
  height: 30px;
}
  .dark .HomePages #menu-bar {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dark .HomePages header .navbar {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    background: #333;
    border-top: 0.1rem solid rgba(255, 255, 255, 0.2);
    padding: 1rem 2rem;
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }
  .dark .HomePages header .navbar.active {
    transition: 0.3s;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .dark .HomePages header .navbar a {
    display: block;
    border-radius: 0.5rem;
    padding: 1.5rem;
    margin: 1.5rem 0;
    background: #222;
  }
  .dark .HomePages .brand-slider {
    width: 100%;
  }
}
@media (max-width: 450px) {
  .dark .HomePages .brand-slider {
    width: 100%;
  }
  html {
    font-size: 50%;
  }
  .dark .HomePages .heading span {
    font-size: 2.5rem;
  }
  .dark .HomePages .contact .row form .inputBox input {
    width: 100%;
  }
}
